/* Third Party */
import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-styled-flexboxgrid'
import styled from 'styled-components'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import Router from 'next/router'
import 'isomorphic-fetch'

import {
    populateRepos
} from '../redux/modules/store'
import RepoCard from '../components/RepoCard'


const PaddedCol = styled(Col)`
    padding-left: 2rem;
`

const UserImg = styled.img`
    border-radius: 0.2rem 0.2rem 0rem 0rem;
    opacity: 1;
    height: 10rem;
`

const Username = styled.p`
    color: black;
    font-size: 2.5rem;
    margin: 1rem;
    margin-top: 3rem;
    margin-bottom: 0rem;
`

const RepoCount = styled(Username)`
    color: gray;
    font-size: 1.2rem;
    margin-top: 0rem;
    margin-left: 1rem;

`

class User extends Component {

    static async getInitialProps({ store, pathname, asPath}) {
        const { currentUser } = store.getState()
        console.log(currentUser)
        // make repos api call here
        const urlRequest = currentUser.repos_url
        await fetch(urlRequest)
            .then(res => res.json())
            .then(repos => {
                store.dispatch(populateRepos(repos))
            })
        return { pathname, asPath }
    }

    render () {
        const {
            props
        } = this

        const {
            pathname,
            asPath,
            currentUser,
            repos
        } = props

        console.log('currentUser: ', currentUser)
        console.log('repos: ', repos)

        const avatarUrl = currentUser.avatar_url
        const username = currentUser.login
        return (
            <Grid>
                <Row start='xs'>
                    <PaddedCol>
                        <UserImg src={avatarUrl} />
                    </PaddedCol>

                    <PaddedCol>
                        <Username> {username} </Username>
                        <RepoCount> {repos.length} Repositories </RepoCount>
                    </PaddedCol>
                </Row>
                    { repos && repos.map((repo, i) => {
                        return (
                            <Row>
                                <Col>
                                    <RepoCard
                                        repo={repo}
                                    />
                                </Col>
                            </Row>
                        )
                    })}
            </Grid>
        )
    }
}

export const mapStateToProps = (state) => {
    return { ...state }
}

export const mapDispatchToProps = (dispatch) => {
    return {
        populateRepos: bindActionCreators(populateRepos, dispatch)
    }
}

export default connect(mapStateToProps)(User)